.display1{
  font-weight: 500;
  font-size: 72px;
}

.display2{
  font-weight: 500;
  font-size: 52px;
  line-height: 119%;
}

.body-text{
  font-size: 20px;
  line-height: 120%;
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 10px;
    }
  }
}

.mlist{
  li{
    line-height: inherit;
    display: flex;
    &::before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #000;
      transform: translateY(8px);
      margin-right: 5px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}


@media screen and (max-width: 1400px){
  .display1{
    font-size: 65px;
  }

  .body-text{
    font-size: 18px;
  }
}

@media screen and (max-width: 1200px){
  .display1{
    font-size: 48px;
  }

  .display2{
    font-size: 42px;
  }
}


@media screen and (max-width: 576px){
  .display1{
    font-size: 38px;
  }

  .display2{
    font-size: 35px;
  }
}

