.main{
  background-image: url(../img/main-bg.svg);
  background-size: cover;
  background-position: center;
  margin-top: -104px;
  padding-top: 100px;
  color: #FFFFFF;
  &__inner{
    padding: 10.91% 0 13.2%;
    display: grid;
    grid-template-columns: calc(100% - 440px) 420px;
    gap: 20px;
  }
  &__title{
    max-width: 1140px;
    span{
      font-size: 87.5%;
      font-weight: 400;
    }
  }
  &__desc{
    margin-top: 51px;
  }
}

.main-form{
  background: rgba(52, 93, 197, 0.6);
  box-shadow: 0px 10px 20px rgba(28, 38, 83, 0.05), inset 0px 4px 24px rgba(184, 204, 255, 0.06);
  backdrop-filter: blur(15px);
  border-radius: 30px;
  padding: 41px 30px 62px;
  &__title{
    font-weight: 800;
    font-size: 22px;
    line-height: 109%;
    text-align: center;
  }
  &__desc{
    font-size: 20px;
    line-height: 120%;
    text-align: center;
  }
  .fg{
    margin-top: 40px;
  }
  &__check{
    margin-top: 54px;
  }
  &__btn{
    margin-top: 54px;
    width: 100%;
    svg{
      width: 24px;
      height: 24px;
      margin-left: 10px;
    }
  }
}

.advantages{
  margin-top: 14%;
  display: grid;
  grid-template-columns: repeat(4,1fr);
  gap: 80px;
}

.slider-block{
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      width: unset;
      max-width: calc(100% - 20px);
    }
  }
}

.trust{
  padding: 65px 0;
  margin-top: 40px;
  overflow: hidden;
  .swiper{
    &-slide{
      width: 240px;
      height: 156px;
      display: flex;
      align-items: center;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-scrollbar{
      margin-top: 79px;
    }
  }
  .slider{
    margin-top: 65px;
  }
}

.geography{
  padding: 65px 0;
  &__map{
    height: 880px;
    margin-top: 56px;
  }
}

.projects{
  padding: 65px 0;
  overflow: hidden;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__title{
    flex-shrink: 0;
  }
  &__links{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  &__link{
    margin: 10px 18px 0 0;
    border-radius: 90px;
    border: $border;
    padding: 0 25px;
    height: 56px;
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 16px;
    img{
      flex-shrink: 0;
      margin-right: 15px;
    }
    &:hover{
      border-color: $black;
      color: $black;
    }
  }
  .slider{
    margin-top: 80px;
  }
  .swiper{
    &-slide{
      width: 533px;
    }
    &-scrollbar{
      margin-top: 99px;
    }
  }
}

.consult{
  background-image: url(../img/main-bg.svg);
  background-size: cover;
  background-position: center;
  color: #fff;
  margin-top: 35px;
  padding: 114px 0 165px;
  &__title{
    text-align: center;
    font-weight: 400;
  }
  &__desc{
    margin: 40px auto 0;
    width: 1213px;
    max-width: 100%;
    font-size: 18px;
    line-height: 156%;
    text-align: center;
  }
}


@media screen and (max-width: 1600px){
  .main{
    margin-top: -93px;
  }

  .advantages{
    gap: 30px;
  }

  .projects{
    &__top{
      display: block;
    }
    &__links{
      margin-top: 20px;
    }
  }
}


@media screen and (max-width: 1400px){
  .main{
    margin-top: -83px;
    &__inner{
      padding: 7% 0 9%;
    }
  }

  .advantages{
    margin-top: 7%;
    grid-template-columns: repeat(3,1fr);
  }
}


@media screen and (max-width: 1200px){
  .main{
    margin-top: -79px;
    padding-top: 80px;
    &__inner{
      padding: 3% 0 5%;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    &__inner{
      grid-template-columns: 100%;
      padding: 0 0 40px;
    }
    &__desc{
      margin-top: 25px;
    }
  }

  .trust{
    padding: 32px 0;
    margin-top: 20px;
  }

  .geography{
    padding: 32px 0;
    &__map{
      height: 500px;
      margin-top: 23px;
    }
  }

  .projects{
    padding: 32px 0;
    .slider{
      margin-top: 40px;
    }
    &__links{
      width: calc(100% + 40px);
      margin-left: -20px;
      overflow-x: auto;
      flex-wrap: unset;
      padding: 0 20px;
      &::-webkit-scrollbar {
        height: 0;
        width: 0;
      }
    }
    &__link{
      margin-right: 10px;
      flex-shrink: 0;
    }
    .swiper-scrollbar{
      margin-top: 50px;
    }
  }
}


@media screen and (max-width: 768px){
  .advantages{
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }

  .trust{
    .slider{
      margin-top: 32px;
    }
    .swiper{
      &-slide{
        height: 130px;
      }
      &-scrollbar{
        margin-top: 40px;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main-form{
    width: calc(100% + 30px);
    margin: 20px 0 0 -15px;
    padding: 25px 15px 30px;
    .fg{
      margin-top: 20px;
    }
    &__check,
    &__btn{
      margin-top: 34px;
    }
  }

  .projects{
    &__links {
      width: calc(100% + 30px);
      margin-left: -15px;
      padding: 0 15px;
    }
  }
}
