.placemark{
  position: absolute;
  font-size: 18px;
  font-weight: 500;
  color: $primary;
  left: -23px;
  top: -23px;
  width: 180px;
  height: 60px;
  background: #FFFFFF;
  min-width: 150px;
  padding: 0 10px;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  svg{
    margin-right: 10px;
  }
}

.ymaps-2-1-79-controls__control,
.ymaps-2-1-79-controls__control_toolbar,
//.ymaps-2-1-79-user-selection-none,
.ymaps-2-1-79-copyright__content,
.ymaps-2-1-79-map-copyrights-promo{
  opacity: 0;
}

.ymaps-2-1-79-balloon{
  box-shadow: none !important;
}

.ymaps-2-1-79-balloon__layout{
  border-radius: 30px !important;
  box-shadow: 0px 8px 30px rgba(73, 101, 126, 0.1) !important;
}

.ymaps-2-1-79-balloon__content{
  padding: 0 !important;
  margin: 0 !important;
}

.ymaps-2-1-79-balloon__close-button{
  background: url(../img/close-white.svg) no-repeat !important;
  background-position: center !important;
  height: 60px !important;
  width: 22px !important;
  opacity: 1 !important;
}

.ymaps-2-1-79-balloon__close{
  position: absolute !important;
  right: 25px;
}

.ymaps-2-1-79-balloon__tail:after,
.ymaps-2-1-79-balloon_to_top .ymaps-2-1-79-balloon__tail{
  display: none;
}

.place-card{
  &__top{
    border-radius: 50px;
    height: 60px;
    display: flex;
    align-items: center;
    background-color: $primary;
    padding: 0 45px 0 20px;
    font-weight: 500;
    font-size: 20px;
    color: #fff;
    min-width: 360px;
  }
  &__icon{
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: #fff;
    flex-shrink: 0;
    margin-right: 10px;
  }
  &__content{
    padding: 16.5px 21px 27.5px;
  }
  &__title{
    font-weight: 500;
    font-size: 16px;
    line-height: 138%;
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 17px;
  }
  &__images{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: -5px;
    img{
      margin: 5px 10px 0 0;
      height: 40px;
      width: auto;
      flex-shrink: 0;
    }
  }
  &__link{
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 15px;
    line-height: 133%;
    white-space: nowrap;
    flex-shrink: 0;
    &:hover{
      path{
        stroke: $primary;
      }
    }
    span{
      transform: translateY(2px);
    }
  }
}


@media screen and (max-width: 992px){
  .place-card{
    &__top{
      min-width: 220px;
    }
  }
}
