@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.social-link{
    width: 27px;
    height: 27px;
    &:hover{
        svg{
            transform: translateY(-10px);
        }
    }
    svg{
        width: 100%;
        height: 100%;
        transition: all .3s;
    }
    &:not(:last-child){
        margin-right: 13px;
    }
}

.mbtn-white{
    background: #FFFFFF;
    border-radius: 60px;
    height: 60px;
    font-weight: 700;
    font-size: 16px;
    color: $primary;
    svg{
        stroke: $primary
    }
    &:hover{
        color: $primary;
        background-color: rgba(#fff,.5);
    }
}
