.logo{
  display: block;
  flex-shrink: 0;
  width: 259px;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  padding: 16.5px 0;
  border-bottom: $border;
  position: relative;
  z-index: 2;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    margin-right: 60px;
  }
  &__mob{
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    border: 1px solid $primary;
    border-radius: 60px;
    width: 155px;
    height: 60px;
    flex-shrink: 0;
    font-weight: 700;
    font-size: 16px;
    color: $primary;
    svg{
      width: 15px;
      height: 15px;
      margin-right: 12px;
      fill: $primary;
    }
    &:hover{
      background-color: $primary;
      color: #fff;
      svg{
        fill: #fff;
      }
    }
  }
  &__phone{
    font-weight: 800;
    font-size: 17px;
    line-height: 118%;
    display: block;
  }
  &__mail{
    margin-top: 6px;
    font-weight: 500;
    font-size: 16px;
    line-height: 125%;
    display: block;
  }
  &__socials{
    display: flex;
    align-items: center;
  }
  &--blue{
    background-color: transparent;
    border: none;
    .nav{
      &__link,
      &__show{
        color: #fff;
        &:hover{
          color: rgba(#fff,.5);
        }
      }
      &__hidden{
        color: $black;
      }
    }
    .header{
      &__phone,
      &__mail{
        color: #fff;
        &:hover{
          color: rgba(#fff,.5);
        }
      }
      &__btn{
        border-color: #fff;
        color: #fff;
        svg{
          fill: #fff;
        }
        &:hover{
          background-color: #fff;
          color: $primary;
          svg{
            fill: $primary;
          }
        }
      }
    }
  }
}

.burger{
  display: none;
}

.nav{
  display: flex;
  align-items: center;
  &__item,
  &__link{
    font-weight: 500;
    font-size: 16px;
    line-height: 125%;
    position: relative;
    &:not(:last-child){
      margin-right: 30px;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    line-height: inherit;
    transition: all .3s;
    svg{
      margin-left: 13px;
    }
    &:hover{
      color: $primary;
      cursor: pointer;
      path{
        stroke: $primary;
      }
    }
  }
  &__hidden{
    position: absolute;
    top: 0;
    left: -30px;
    padding-top: 44px;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &-inner{
      background: #FFFFFF;
      border: 1px solid rgba(207, 218, 252, 0.5);
      box-shadow: 0px 35px 30px 5px rgba(52, 93, 197, 0.05);
      border-radius: 30px;
      padding: 15px 0;
    }
  }
  &__sublink{
    min-width: 260px;
    padding: 4px 30px;
    min-height: 42px;
    display: flex;
    align-items: center;
    font-size: 15px;
    line-height: 133%;
    &:hover{
      font-weight: 500;
      background: #F4F7FF;
    }
  }
  &__item{
    &--active{
      .nav__show{

      }
      .nav__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}


@media screen and (max-width: 1600px){
  .logo{
    width: 210px;
  }

  .header{
    &__logo{
      margin-right: 20px;
    }
  }

  .nav{
    &__item,
    &__link{
      &:not(:last-child){
        margin-right: 15px;
      }
    }
  }
}


@media screen and (max-width: 1400px){
  .header{
    &__logo{
      width: 170px;
      margin-right: 10px;
    }
    @media screen and (min-width: 1201px){
      &__btn{
        width: 50px;
        height: 50px;
        border-radius: 50%;
        svg{
          margin: 0;
        }
        span{
          display: none;
        }
      }
    }

  }
}

@media screen and (max-width: 1200px){
  .header{
    transition: all .3s;
    &__mob{
      position: fixed;
      top: 78px;
      left: 0;
      width: 100%;
      height: calc(100vh - 78px);
      background-color: #fff;
      display: block;
      padding: 20px;
      overflow-y: auto;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
    &__socials,
    &__contacts{
      margin-top: 20px;
    }
    &__btn{
      margin-top: 30px;
      width: 100%;
    }
    &--active{
      .burger{
        span{
          &:nth-child(1){
            transform: rotate(-45deg) translate(-8px,5px);
          }
          &:nth-child(2){
            transform: scale(0);
          }
          &:nth-child(3){
            transform: rotate(45deg) translate(-8px,-5px);
          }
        }
      }
      .header{
        &__mob{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    &--blue{
      &.header--active{
        background-color: $primary;
      }
      .header{
        &__mob{
          background-color: $primary;
        }
      }
      .burger{
        span{
          background-color: #fff;
        }
      }
    }
  }

  .burger{
    margin-left: auto;
    width: 24px;
    height: 20px;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 15;
    span{
      display: block;
      width: 100%;
      height: 1.31px;
      background: $primary;
      left: 0;
      transition: all .3s;
      &:nth-child(1){
        position: absolute;
        top: 0;
      }
      &:nth-child(3){
        position: absolute;
        bottom: 0;
      }
    }
  }

  .nav{
    flex-direction: column;
    &__link,
    &__item{
      width: 100%;
      font-size: 20px;
      &:not(:last-child){
        margin: 0 0 20px;
      }
    }
    &__hidden{
      opacity: 1;
      display: none;
      pointer-events: unset;
      transition: unset;
      position: static;
      padding: 0;
      width: 100%;
      margin-top: 5px;
      &-inner{
        border-radius: 0;
        padding: 0;
      }
    }
    &__sublink{
      width: 100%;
      padding-left: 15px;
      padding-right: 15px;
    }
  }
}
