.fg {
  position: relative;
  margin: 0;
  input,
  select,
  textarea{
    border: none;
    border-bottom: 1px solid rgba(#fff,.2);
    padding: 0;
    height: 52px;
    width: 100%;
    font-size: 16px;
    line-height: 150%;
    background-color: transparent;
    color: #FFFFFF;
    &::placeholder {
      color: #FFFFFF;
    }
  }
  textarea{
    padding: 23px 0;
    resize: none;
    height: 100px;
  }

  &__icon{
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 15px;
    &~input{
      padding-left: 28px;
    }
  }
  &--white{
    select,
    input,
    textarea{
      border-color: #fff;
      color: #fff;
      &::placeholder{
        color: #fff;
      }
    }
    select{
      option{
        padding: 0 10px;
        background: $black;
      }
    }
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        opacity: 1;
        background-image: url(../img/check.svg);
        background-repeat: no-repeat;
        background-position: center;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 27px;
    position: relative;
    min-height: 20px;
    font-size: 16px;
    line-height: 150%;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border: 1px solid #fff;
      width: 17px;
      height: 17px;
      opacity: .2;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}
