.advantage-card{
  max-width: 220px;
  &__icon{
    display: block;
    height: 80px;
    margin-bottom: 40px;
    img{
      width: auto;
      height: 100%;
      display: block;
    }
  }
}

.project-card{
  border-radius: 20px;
  overflow: hidden;
  &__img{
    position: relative;
    padding-bottom: 108.85%;
    &:hover{
      img{
        transform: scale(1.1);
      }
    }
    img{
      transition: all .3s;
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
    }
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(11, 21, 30, 0) 0%, rgba(11, 21, 30, 0.8) 100%);
      z-index: 2;
    }
  }
  &__content{
    position: absolute;
    z-index: 3;
    padding: 42px;
    bottom: 0;
    left: 0;
  }
  &__images{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    img{
      margin: 10px 15px 0 0;
      flex-shrink: 0;
      height: 40px;
      width: auto;
    }
  }
  &__title{
    display: block;
    font-weight: 500;
    font-size: 22px;
    line-height: 118%;
    margin-top: 25px;
    color: #fff;

  }
  &__link{
    display: flex;
    align-items: center;
    margin-top: 27px;
    font-weight: 700;
    font-size: 15px;
    line-height: 133%;
    color: #fff;
    svg{
      width: 24px;
      height: 24px;
      stroke: #fff;
    }
    &:hover{
      color: rgba(#fff,.5);
      svg{
        stroke: rgba(#fff,.5);
      }
    }
  }
}


@media screen and (max-width: 576px){
  .advantage-card{
    &__icon{
      height: 60px;
      margin-bottom: 20px;
    }
  }

  .project-card{
    &__content{
      padding: 15px;
    }
    &__title{
      font-size: 18px;
      margin-top: 15px;
    }
    &__link{
      margin-top: 15px;
    }
  }
}
